jQuery(document).ready(function() {

    // add Foundation 6 equal height attributes using custom classes
    jQuery('.eq-parent').each(function() {
        
        jQuery(this).attr('data-equalizer', '');

        jQuery(this).find('.eq-child').attr('data-equalizer-watch', '');

        var igEqualHeight = new Foundation.Equalizer(jQuery(this));

        jQuery(igEqualHeight).foundation();
        
    });    
    
});