jQuery('.iusa-live-chat').click(function(event) {

	if (iusaEnvironment == 'DEV' || iusaEnvironment == 'TEST') {

		var iusaChatURL = 'https://test-chat.infousa.com/i3root/infousa/';

	} else {
	
		var iusaChatURL = 'https://sharedcomponents-srv.infousa.com/Chat/Chat.aspx';

	}
	
	var objWin = window.open(
		iusaChatURL + '?product=InfoUSA%2c+Inc&logo=https://s3.amazonaws.com/marketing.infogroup.com/cms/iusa/chat/infousa-chat-logo.png?accountname=&platform=cms',
		'MyWindow1427656777',
		'toolbar=0,location=0,directories=0,status=0,menubar=0,scrollbars=0,resizable=1,left=' + ((screen.width - 470) / 2) + ',width=470px,top=' + ((screen.height - 430) / 2) + ',height=430px');

	if (objWin != null) {
		objWin.window.focus();
	}

	event.preventDefault();

});